import {DEVICE_TYPE} from "../actions/types";

const INITIAL_STATE = {
    isMobileDevice: false,
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case DEVICE_TYPE:
            console.log("setting device type: "+action.isMobileDevice);
            return {...state, isMobileDevice: action.isMobileDevice};
        default:
            return state;
    }
}